//    var player = videojs('my-player');
    const Waves  = require('node-waves');
    import Vue from 'vue'

    import live_list from './live_app/live_list.vue'
    import live_info from './live_app/live_info.vue'
    import live_video from './live_app/live_video.vue'
    import live_ctrl from './live_app/live_ctrl.vue'
    export default {

        data() {
            return {
                video_list:[],
                ctrl:false


            }
        },
        methods:{
            actionctrl:function () {
                this.ctrl = !this.ctrl
            },
        },
        mounted(){
            var vm = this
            this.$root.eventHub.$on('touchCtrl',function (data) {
                console.log('touchCtrl')
                vm.ctrl = !vm.ctrl
            })
            if( this.$route.query.mode === 'auto'){
                this.ctrl = true

                Vue.nextTick(function () {
                    console.log('start do autoInit')
                    vm.$root.eventHub.$emit('autoInit', '')
                    vm.ctrl = false
                })

            }
            var Waves_config = {
                // How long Waves effect duration
                // when it's clicked (in milliseconds)
                duration: 500,

                // Delay showing Waves effect on touch
                // and hide the effect if user scrolls
                // (0 to disable delay) (in milliseconds)
                delay: 200
            };

// Initialise Waves with the config
            Waves.init(Waves_config);

            Waves.attach('.button', ['waves-button', 'waves-float'])

        },
        created(){

        },
        computed:{

        },
        components:{
            live_list,
            live_video,
            live_info,
            live_ctrl
        }
    }



